import React from 'react';
import {differenceInHours} from 'date-fns';
import Donut from './Donut';
import './Period.css'

const getFirstLetter = string => string.slice(0, 1);

const Period = ({date, period, onChartClick}) => {
  const {name, start, end, difference, unit, color} = period;

  const startDate = start(date);
  const endDate = end(date);

  const unitsLeft = difference(endDate, date) + 1;
  const percentageLeft = differenceInHours(endDate, date) / differenceInHours(endDate, startDate);

  return (
    <section className="period">
      <Donut
        className="period__chart"
        percentage={percentageLeft}
        color={color}
        strokeWidth={8}
        text={`${unitsLeft} ${getFirstLetter(unit)}`}
        onClick={onChartClick}
      />

      <h3 className="period__text">
        {name}
      </h3>
    </section>
  );
};

export default Period;




