import {
  startOfWeek,
  endOfWeek,
  startOfMonth,
  endOfMonth,
  startOfQuarter,
  endOfQuarter,
  startOfYear,
  endOfYear,
  differenceInMinutes,
  differenceInHours,
  differenceInCalendarDays,
  differenceInCalendarWeeks,
  differenceInCalendarMonths,
} from 'date-fns';

const MINUTE_ID = 'minute';
const HOUR_ID = 'hour';
const DAY_ID = 'day';
const WEEK_ID = 'week';
const MONTH_ID = 'month';
const QUARTER_ID = 'quarter';
const YEAR_ID = 'year';

const MINUTE = {name: MINUTE_ID, difference: differenceInMinutes};
const HOUR = {name: HOUR_ID, difference: differenceInHours};
const DAY = {name: DAY_ID, difference: differenceInCalendarDays};
const WEEK = {name: WEEK_ID, difference: differenceInCalendarWeeks};
const MONTH = {name: MONTH_ID, difference: differenceInCalendarMonths};

const PERIODS_CONFIG = {
  [WEEK_ID]: {
    id: WEEK_ID,
    name: 'Week',
    start: date => startOfWeek(date, {weekStartsOn: 1}),
    end: date => endOfWeek(date, {weekStartsOn: 1}),
    units: [MINUTE, HOUR, DAY],
    defaultUnit: DAY_ID,
    color: '#2196F3',
  },
  [MONTH_ID]: {
    id: MONTH_ID,
    name: 'Month',
    start: startOfMonth,
    end: endOfMonth,
    units: [HOUR, DAY, WEEK],
    defaultUnit: DAY_ID,
    color: '#E91E63',
  },
  [QUARTER_ID]: {
    id: QUARTER_ID,
    name: 'Quarter',
    start: startOfQuarter,
    end: endOfQuarter,
    units: [DAY, WEEK, MONTH],
    defaultUnit: WEEK_ID,
    color: '#009688',
  },
  [YEAR_ID]: {
    id: YEAR_ID,
    name: 'Year',
    start: startOfYear,
    end: endOfYear,
    units: [DAY, WEEK, MONTH],
    defaultUnit: WEEK_ID,
    color: '#FF5722',
  },
};

export const getPeriodIds = () => Object.keys(PERIODS_CONFIG);

export const getPeriod = periodId => PERIODS_CONFIG[periodId];

export const getPeriodUnits = periodId => getPeriod(periodId).units;

export const getPeriodDefaultUnit = periodId => getPeriod(periodId).defaultUnit;



