import React, { Component, Fragment } from 'react';
import Header from './Header';
import Periods from './Periods';
import {
  getPeriodIds,
  getPeriod,
  getPeriodUnits,
  getPeriodDefaultUnit,
} from './config';
import {
  loadPeriodUnit,
  savePeriodUnit,
  clearPeriodUnits,
} from './storage';

const getPeriodUnitAndDifferenceFn = (periodId, unitName) => {
  const periodUnits = getPeriodUnits(periodId);
  const {name: unit, difference} = periodUnits.find(unit => unit.name === unitName);
  return {unit, difference};
};

const getInitialPeriods = () =>
  getPeriodIds().reduce((periods, periodId) => {
    periods[periodId] = {
      ...getPeriod(periodId),
      ...getPeriodUnitAndDifferenceFn(periodId, loadPeriodUnit(periodId) || getPeriodDefaultUnit(periodId)),
    };
    return periods;
  }, {});

const arePeriodUnitsDefault = (periods) =>
  Object.keys(periods).every(periodId => periods[periodId].unit === getPeriodDefaultUnit(periodId));

class App extends Component {
  state = {
    currentDate: new Date(),
    periods: getInitialPeriods(),
  };

  componentDidMount() {
    this.interval = setInterval(this.updateDate, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  updateDate = () => {
    this.setState({
      currentDate: new Date(),
    });
  };

  setPeriod = (periodId, calculatePeriod) => {
    this.setState(({periods}) => ({
      periods: {
        ...periods,
        [periodId]: calculatePeriod(periods),
      },
    }));
  };

  setNextPeriodUnit = (periodId) => {
    this.setPeriod(periodId, currentPeriods => {
      const periodUnits = getPeriodUnits(periodId);
      const currentUnitName = currentPeriods[periodId].unit;
      const currentUnitIndex = periodUnits.findIndex(unit => unit.name === currentUnitName);
      const {name: unitName} = periodUnits[currentUnitIndex + 1] || periodUnits[0];

      savePeriodUnit(periodId, unitName);

      return {
        ...currentPeriods[periodId],
        ...getPeriodUnitAndDifferenceFn(periodId, unitName),
      };
    });
  };

  setDefaultPeriodUnits = () => {
    clearPeriodUnits();

    this.setState({
      periods: getInitialPeriods(),
    });
  };

  render() {
    const {currentDate, periods} = this.state;

    return (
      <Fragment>
        <Header
          date={currentDate}
          onResetPeriodUnits={
            arePeriodUnitsDefault(periods) ? undefined : this.setDefaultPeriodUnits
          }
        />
        <Periods
          date={currentDate}
          periods={periods}
          onPeriodClick={this.setNextPeriodUnit}
        />
      </Fragment>
    );
  }
}

export default App;



