import React from 'react';
import Period from './Period';
import './Periods.css'

const Periods = ({date, periods, onPeriodClick}) => (
  <main className="periods">
    {Object.keys(periods).map((periodKey) => (
      <Period
        key={periodKey}
        date={date}
        period={periods[periodKey]}
        onChartClick={() => onPeriodClick(periodKey)}
      />
    ))}
  </main>
);

export default Periods;



