import React, {Component} from 'react';
import './Donut.css';

const BASE_SIZE = 100;
const ANGLE_FACTOR = 2.89;
const FULL_CIRCLE_DEG = 360;

const StaticDonut = ({className, percentage, color, strokeWidth, text, onClick}) => {
  const viewBox = `0 0 ${BASE_SIZE} ${BASE_SIZE}`;
  const cx = BASE_SIZE / 2;
  const cy = BASE_SIZE / 2;
  const r = (BASE_SIZE - strokeWidth) / 2;
  const strokeDasharray = BASE_SIZE * ANGLE_FACTOR;
  const strokeDashoffset = (strokeDasharray / FULL_CIRCLE_DEG) * (FULL_CIRCLE_DEG - percentage * FULL_CIRCLE_DEG);

  return (
    <div className={`donut ${className || ''}`}>
      <h2
        className="donut__text"
        style={{color}}
      >
        {text}
      </h2>

      <svg
        className="donut__graph"
        viewBox={viewBox}
        xmlns="http://www.w3.org/2000/svg"
        onClick={onClick}
      >
        <circle
          className="donut__graph__background"
          cx={cx}
          cy={cy}
          r={r}
          strokeWidth={strokeWidth}
        />
        <circle
          className="donut__graph__fill"
          cx={cx}
          cy={cy}
          r={r}
          strokeWidth={strokeWidth}
          strokeDasharray={strokeDasharray}
          strokeDashoffset={strokeDashoffset}
          stroke={color}
        />
      </svg>
    </div>
  );
};

class Donut extends Component {
  state = {
    percentage: 1,
  };

  componentDidMount() {
    setTimeout(() => {
      this.setPercentage(this.props.percentage);
    }, 500);
  }

  componentWillReceiveProps(nextProps) {
    this.setPercentage(nextProps.percentage);
  }

  setPercentage = percentage => {
    this.setState({percentage});
  };

  render() {
    const {percentage, ...props} = this.props;
    return (
      <StaticDonut
        percentage={this.state.percentage}
        {...props}
      />
    );
  }
}

export default Donut;



