import React from 'react';
import {format} from 'date-fns';
import './Header.css';

const HeaderButton = ({isVisible, onClick}) => (
  <button
    className={`header__button header__button--${isVisible ? 'visible' : 'hidden'}`}
    onClick={onClick}
  >
    Reset defaults
  </button>
);

const getTitle = date => format(date, 'MMMM Do');

const getSubtitle = date => format(date, 'dddd, h:mm a');

const Header = ({date, onResetPeriodUnits}) => (
  <header className="header">
    <h1 className="header__title">
      {getTitle(date)}
    </h1>
    <h2 className="header__subtitle">
      {getSubtitle(date)}
    </h2>
    <HeaderButton
      isVisible={!!onResetPeriodUnits}
      onClick={onResetPeriodUnits}
    />
  </header>
);

export default Header;


